<template>
  <el-container style="height: 100vh;">
    <el-aside width="700px" class="left-panel">
      <el-form>
        <el-text class="mx-1" size="large">telegram机器人Token</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.tgBotToken" placeholder="12345678:AAGC8NFfxxxx" clearable></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">telegram个人ID</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.tgChatId" placeholder="61945xxxxx" clearable></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">钉钉access_token</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.dingToken" placeholder="0b0bf9f89xxxx" clearable></el-input>
        </el-form-item>
        <el-text class="mx-1" size="large">钉钉secret</el-text>
        <el-form-item style="margin-top: 10px">
          <el-input v-model="UpdateSysCfgParams.dingSecret" placeholder="SEC1ebxxxx" clearable></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="updateSysCfg" :loading="cfgLoading">更新配置</el-button>
    </el-aside>
    <el-main class="right-panel">
      <el-input type="textarea" v-model="message" placeholder="输入测试文本" :rows="20"></el-input>
      <el-button type="primary" @click="sendMsg" :loading="sendLoading">测试消息发送</el-button>
    </el-main>
  </el-container>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      cfgLoading: false,
      sendLoading: false,
      UpdateSysCfgParams: {
        dingToken: '',
        dingSecret: '',
        tgChatId: '',
        tgBotToken: ''
      },
      message: ''
    };
  },
  mounted() {
    this.getSysCfg()
  },
  methods: {
    async getSysCfg() {
      await this.$axios.post('/sys/getSysCfg', {})
          .then(response => {
            if (response.data.success) {
              // ElMessage({
              //   message: response.data.msg,
              //   type: 'success',
              //   duration: 2000 // 设置提示显示时间，单位是毫秒
              // });
              this.UpdateSysCfgParams = response.data.data
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateSysCfg() {
      this.cfgLoading = true
      await this.$axios.post('/sys/updateSysCfg', {...this.UpdateSysCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.cfgLoading = false
              this.getSysCfg()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async sendMsg() {
      this.sendLoading = true
      await this.$axios.post('/sys/sendMsg', {message: this.message})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.sendLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
};
</script>

<style scoped>
.left-panel, .right-panel {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}

.right-panel {
  flex: 1;
}

.el-input[type="textarea"] {
  flex: 1;
  resize: none;
}
</style>
